import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import loadable from "@loadable/component";
import Reliance from "../../Assets/Projects/Reliance.jpg";
import Cricket from '../../Assets/Projects/Cricket.jpg';
import Expedia from '../../Assets/Projects/Expedia.jpg'

const ProjectCard = loadable(() => import("./ProjectCards"));

const projects = [
  {
    imgPath: Reliance,
    title: "Reliance-Mart Clone",
    description: "Reliance-Mart is an indian e-commerce company, using this clone user can login, search and see products, add them to cart and purchase them.",
    link: "https://github.com/Sonia-saini/-accidental-visitor-9960",
    demo: "https://radiant-semolina-0581db.netlify.app/",
    stack: ["react", "redux", "mui", "git"]
  },
  {
    imgPath: Expedia,
    title: "Expedia.com Clone",
    description: "Expedia.com is a American travelling agency website which helps to books flight, car, hotel an city in the world.",
    link: "https://github.com/Shishir-1995/Expedia",
    demo: "https://expedia-masai.netlify.app/index.html",
    stack: ["javascript", "html", "css", "git"]
  },
  {
    imgPath: Cricket,
    title: "Cricket.com Clone",
    description: "Cricket.com is a cricket news website in which you can see the live score, matche schedule etc.",
    link: "https://github.com/saurabhpawar1997/mixed-plant-9533",
    demo: "https://cricket-c0a257.netlify.app/index.html",
    stack: ["javascript", "html", "css", "git"]
  }
];


function Projects() {
  return (
    <Container id="projects" fluid className="project-section">
      <Container>
        <h1 className="project-heading">
          My Recent <strong className="purple">Works </strong>
        </h1>
        <p style={{ color: "white" }}>
          Here are a few projects I've worked on recently.
        </p>
        <Row style={{ justifyContent: "center", gap: "0.5rem" }}>
          {
            projects.map(project => (
              <Col key={project.title} xs={12} sm={10} lg={6} className="project-card">
                <ProjectCard
                  {...project}
                />
              </Col>
            ))
          }
        </Row>
      </Container>
    </Container>
  );
}

export default Projects;
